/**
 * ContentManager Component
 * Handles content management functionality for admin panel
 */

class ContentManager {
  constructor(container, options = {}) {
    this.container = container;
    this.options = {
      apiEndpoint: '/api/articles',
      enableDrafts: true,
      enableScheduling: true,
      ...options,
    };

    this.articles = [];
    this.currentFilter = 'all';
    this.init();
  }

  init() {
    this.setupUI();
    this.loadArticles();
    this.setupEventListeners();
  }

  setupUI() {
    this.container.innerHTML = `
      <div class="content-manager">
        <div class="content-manager__header">
          <h2>Content Manager</h2>
          <div class="content-manager__actions">
            <button class="btn btn--primary" data-action="new-article">
              New Article
            </button>
            <button class="btn btn--secondary" data-action="refresh">
              Refresh
            </button>
          </div>
        </div>
        
        <div class="content-manager__filters">
          <button class="filter-btn active" data-filter="all">All</button>
          <button class="filter-btn" data-filter="published">Published</button>
          <button class="filter-btn" data-filter="draft">Drafts</button>
          <button class="filter-btn" data-filter="scheduled">Scheduled</button>
        </div>
        
        <div class="content-manager__search">
          <input type="text" placeholder="Search articles..." class="search-input">
        </div>
        
        <div class="content-manager__list">
          <div class="loading">Loading articles...</div>
        </div>
      </div>
    `;
  }

  setupEventListeners() {
    // Action buttons
    this.container.addEventListener('click', (e) => {
      const action = e.target.dataset.action;
      if (action) {
        this.handleAction(action, e.target);
      }
    });

    // Filter buttons
    this.container.addEventListener('click', (e) => {
      if (e.target.classList.contains('filter-btn')) {
        this.setFilter(e.target.dataset.filter);
        this.updateFilterButtons(e.target);
      }
    });

    // Search input
    const searchInput = this.container.querySelector('.search-input');
    if (searchInput) {
      searchInput.addEventListener('input', (e) => {
        this.filterArticles(e.target.value);
      });
    }
  }

  async loadArticles() {
    try {
      const response = await fetch(this.options.apiEndpoint);
      if (response.ok) {
        this.articles = await response.json();
        this.renderArticles();
      } else {
        throw new Error('Failed to load articles');
      }
    } catch (error) {
      console.error('Error loading articles:', error);
      this.showError('Failed to load articles. Please try again.');
    }
  }

  renderArticles() {
    const listContainer = this.container.querySelector(
      '.content-manager__list'
    );

    if (this.articles.length === 0) {
      listContainer.innerHTML =
        '<div class="empty-state">No articles found.</div>';
      return;
    }

    const filteredArticles = this.getFilteredArticles();

    listContainer.innerHTML = filteredArticles
      .map(
        (article) => `
      <div class="article-item" data-id="${article._id}">
        <div class="article-item__content">
          <h3 class="article-item__title">${article.title}</h3>
          <p class="article-item__meta">
            <span class="status status--${article.status}">${
          article.status
        }</span>
            <span class="date">${this.formatDate(article.created_at)}</span>
          </p>
          <p class="article-item__summary">${
            article.summary || 'No summary available'
          }</p>
        </div>
        <div class="article-item__actions">
          <button class="btn btn--small" data-action="edit" data-id="${
            article._id
          }">
            Edit
          </button>
          <button class="btn btn--small btn--danger" data-action="delete" data-id="${
            article._id
          }">
            Delete
          </button>
        </div>
      </div>
    `
      )
      .join('');
  }

  getFilteredArticles() {
    let filtered = this.articles;

    // Apply status filter
    if (this.currentFilter !== 'all') {
      filtered = filtered.filter(
        (article) => article.status === this.currentFilter
      );
    }

    return filtered;
  }

  filterArticles(searchTerm) {
    const listContainer = this.container.querySelector(
      '.content-manager__list'
    );
    const items = listContainer.querySelectorAll('.article-item');

    items.forEach((item) => {
      const title = item
        .querySelector('.article-item__title')
        .textContent.toLowerCase();
      const summary = item
        .querySelector('.article-item__summary')
        .textContent.toLowerCase();
      const matches =
        title.includes(searchTerm.toLowerCase()) ||
        summary.includes(searchTerm.toLowerCase());

      item.style.display = matches ? 'block' : 'none';
    });
  }

  setFilter(filter) {
    this.currentFilter = filter;
    this.renderArticles();
  }

  updateFilterButtons(activeButton) {
    this.container.querySelectorAll('.filter-btn').forEach((btn) => {
      btn.classList.remove('active');
    });
    activeButton.classList.add('active');
  }

  async handleAction(action, element) {
    const articleId = element.dataset.id;

    switch (action) {
      case 'new-article':
        this.createNewArticle();
        break;
      case 'edit':
        this.editArticle(articleId);
        break;
      case 'delete':
        if (confirm('Are you sure you want to delete this article?')) {
          await this.deleteArticle(articleId);
        }
        break;
      case 'refresh':
        await this.loadArticles();
        break;
    }
  }

  createNewArticle() {
    // Dispatch event for article editor
    const event = new CustomEvent('openArticleEditor', {
      detail: { mode: 'create' },
    });
    document.dispatchEvent(event);
  }

  editArticle(articleId) {
    const article = this.articles.find((a) => a._id === articleId);
    if (article) {
      const event = new CustomEvent('openArticleEditor', {
        detail: { mode: 'edit', article },
      });
      document.dispatchEvent(event);
    }
  }

  async deleteArticle(articleId) {
    try {
      const response = await fetch(`${this.options.apiEndpoint}/${articleId}`, {
        method: 'DELETE',
      });

      if (response.ok) {
        this.articles = this.articles.filter((a) => a._id !== articleId);
        this.renderArticles();
        this.showSuccess('Article deleted successfully');
      } else {
        throw new Error('Failed to delete article');
      }
    } catch (error) {
      console.error('Error deleting article:', error);
      this.showError('Failed to delete article. Please try again.');
    }
  }

  formatDate(dateString) {
    const date = new Date(dateString);
    return date.toLocaleDateString() + ' ' + date.toLocaleTimeString();
  }

  showError(message) {
    this.showNotification(message, 'error');
  }

  showSuccess(message) {
    this.showNotification(message, 'success');
  }

  showNotification(message, type = 'info') {
    const notification = document.createElement('div');
    notification.className = `notification notification--${type}`;
    notification.textContent = message;
    notification.style.cssText = `
      position: fixed;
      top: 20px;
      right: 20px;
      padding: 12px 24px;
      border-radius: 4px;
      z-index: 1000;
      transition: opacity 0.3s;
      background: ${
        type === 'error'
          ? '#f44336'
          : type === 'success'
          ? '#4CAF50'
          : '#2196F3'
      };
      color: white;
    `;

    document.body.appendChild(notification);

    setTimeout(() => {
      notification.style.opacity = '0';
      setTimeout(() => {
        if (document.body.contains(notification)) {
          document.body.removeChild(notification);
        }
      }, 300);
    }, 3000);
  }
}

// Export for use in other modules
if (typeof module !== 'undefined' && module.exports) {
  module.exports = ContentManager;
} else {
  window.ContentManager = ContentManager;
}
