// CSS is handled by the build process
const ContentManager = require('../components/admin/ContentManager.js');
const ArticleEditor = require('../components/admin/ArticleEditor.js');
const ErrorHandler = require('../services/error/ErrorHandler.js');
class AdminPanel {
  constructor() {
    this.init();
  }
  init() {
    try {
      const contentManagerContainer =
        document.querySelector('.content-manager');
      if (contentManagerContainer) {
        this.contentManager = new ContentManager(contentManagerContainer);
      }
      const editorContainer = document.querySelector('.article-editor');
      if (editorContainer) {
        this.articleEditor = new ArticleEditor(editorContainer);
      }
      this.setupEventListeners();
      console.log('Admin panel initialized successfully');
    } catch (error) {
      ErrorHandler.handle(error, { context: 'Admin panel initialization' });
    }
  }
  setupEventListeners() {
    const adminNavLinks = document.querySelectorAll('.admin-nav a');
    adminNavLinks.forEach((link) => {
      link.addEventListener('click', this.handleNavigation.bind(this));
    });
    const bulkActionButtons = document.querySelectorAll('.bulk-action');
    bulkActionButtons.forEach((button) => {
      button.addEventListener('click', this.handleBulkAction.bind(this));
    });
  }
  handleNavigation(event) {
    event.preventDefault();
    const target = event.currentTarget.getAttribute('href');
    document.querySelectorAll('.admin-section').forEach((section) => {
      section.classList.add('hidden');
    });
    const targetSection = document.querySelector(target);
    if (targetSection) {
      targetSection.classList.remove('hidden');
    }
    document.querySelectorAll('.admin-nav a').forEach((link) => {
      link.classList.remove('active');
    });
    event.currentTarget.classList.add('active');
  }
  handleBulkAction(event) {
    const action = event.currentTarget.dataset.action;
    const selectedItems = document.querySelectorAll('.item-checkbox:checked');
    if (selectedItems.length === 0) {
      alert('Please select items to perform bulk action');
      return;
    }
    if (
      confirm(`Are you sure you want to ${action}${selectedItems.length}items?`)
    ) {
      console.log(`Performing ${action}on ${selectedItems.length}items`);
    }
  }
}
if (document.readyState === 'loading') {
  document.addEventListener('DOMContentLoaded', () => new AdminPanel());
} else {
  new AdminPanel();
}
// Export for both CommonJS and ES modules
if (typeof module !== 'undefined' && module.exports) {
  module.exports = AdminPanel;
} else if (typeof window !== 'undefined') {
  window.AdminPanel = AdminPanel;
}
